<template>
	<div class="EditBlog">
		<van-nav-bar fixed placeholder left-text="返回" @click-left="onClickLeft" left-arrow title="编辑博客"></van-nav-bar>
		<!-- 编辑前显示 -->
		<form v-if="fb">
			<label for="title">标题</label>
			<input type="text" name="title" id="title" v-model="blog.title" required/>
			<label for="content">内容</label>
			<textarea type="text" name="content" id="content" v-model="blog.body" required/>
			<van-button class='but' type="primary" @click.prevent="edit">编辑</van-button>
		</form>
		<!-- 编辑成功显示 -->
		<div v-else-if="!fb" class="block">
			<div>博客编辑成功</div>
		</div>
		<!--点击编辑后显示加载中的遮罩层-->
		<van-overlay :show="show">
			<div class="wrapper" @click.stop>
				<van-loading type="spinner" color="#1989fa"/>
			</div>
		</van-overlay>
	</div>
</template>

<script>
	export default{
		name:"EditBlog",
		data(){
			return{
				id:this.$route.params.id,
				blog:{},
				show: false,
				fb:true
			}
		},
		methods:{
			onClickLeft(){
				this.$router.back(-1)
			},
			//leancloud 点击提交编辑按钮
			edit(){
				if(this.blog.title==''){
					this.$toast('请输入标题')
				}else if(this.blog.body==''){
					this.$toast('请输入内容')
				}else{
				// 加载中的遮罩层
				this.show=true;
				
				const AV=this.$AV;
				const blog = AV.Object.createWithoutData('Blogs',this.id);
				blog.set(this.blog);
				blog.save();
				
				this.show=false;
				this.fb=false;
				this.$notify({ type: 'success', message: '编辑成功' });
				}
			}
		},
		created(){
			// leancloud
			// 获取数据渲染页面
			const AV=this.$AV;
			const query = new AV.Query('Blogs');
			query.get(this.id).then((res) => {
				var res=res.toJSON();
				var text={};
				text['title']= res.title;
				text['body']= res.body;
				this.blog=text;
			});
		}
	}
</script>

<style scoped>
	td{table-layout : fixed}
	form{padding: 50px;padding-top:0;text-align: center;}
	form textarea,form input{display: block;width:548px;}
	form label{display: block;font-size:50px;}
	form input,form textarea{font-size:30px;border: 1px solid gainsboro;padding:50px;}
	form textarea{resize:none;height:300px;}
	form>label,form>input,form>textarea,form>.but{
		margin-top: 50px;
	}
	.wrapper {
	    display: flex;
	    align-items: center;
	    justify-content: center;
	    height: 100%;
	}
	.block{
		display: flex;
		align-items: center;
		justify-content: center;
		height:500px;
	}
	.block div{
		display: block;
		font-size:36px;
		color: #07c160;
	}
</style>
