<template>
	<div class="Footer">
		<van-tabbar v-model="active" fixed placeholder safe-area-inset-bottom>
			<van-tabbar-item icon="home-o" replace to="/">首页</van-tabbar-item>
			<van-tabbar-item icon="add-o" replace to="/AddBlog">添加</van-tabbar-item>
		</van-tabbar>
	</div>
</template>

<script>
	export default{
		name:"Footer",
		data(){
			return{
				active:0
			}
		},
		watch:{
			$route(to){
				var path=to.path;
				if(path=='/'){
					this.active=0;
				}
				if(path=='/AddBlog'){
					this.active=1;
				}
			}
		},
	}
</script>

