<template>
	<div class="ShowBlog">
		<van-nav-bar fixed placeholder left-text="首页" @click-left="onClickLeft" left-arrow title="博客内容"></van-nav-bar>
		
		<template v-if="fb">
			<van-divider :style="{ color: '#1989fa', borderColor: '#1989fa', padding: '0 16px' }">
				<h3>正文</h3>
			</van-divider>
			<div class="blog">
				<h4 v-rainbow>{{blog.title}}</h4>
				<p>发布日期：{{blog.createdTime}}</p>
				<p>上次编辑：{{blog.editTime}}</p>
				<article>{{blog.body}}</article>
			</div>
			<div class="cz">
				<van-button class="but" type="danger" @click="deleteBlog">删除</van-button>
				<van-button class="but" type="warning" :to="'/EditBlog/' + this.id">编辑</van-button>
			</div>
		</template>

		
		<!-- 删除成功显示 -->
		<div v-else-if="!fb" class="block">
			<div>博客删除成功</div>
		</div>
		<!--点击删除后显示加载中的遮罩层-->
		<van-overlay :show="show">
			<div class="wrapper" @click.stop>
				<van-loading type="spinner" color="#1989fa"/>
			</div>
		</van-overlay>
	</div>
</template>

<script>
	export default{
		name:"ShowBlog",
		data(){
			return{
				id:this.$route.params.id,
				blog:{},
				show: false,
				fb:true
			}
		},
		methods:{
			onClickLeft() {
				this.$router.back(-1)
			},
			deleteBlog(){
				var id=this.blog.id
				
				// leancloud删除
				const AV=this.$AV;
				const blog = AV.Object.createWithoutData('Blogs',id);
				blog.destroy();

				this.show=false;
				this.fb=false;
				this.$notify({ type: 'success', message: '删除成功' });
			}
		},
		created(){
			// var that=this;
			// this.$axios('/posts/'+this.id)
			// .then(function(res){
			// 	that.blog=res.data
			// }).catch(function(err){
			// 	console.log(err)
			// })

			// leancloud
			const AV=this.$AV;
			const query = new AV.Query('Blogs');

			query.get(this.id).then((res) => {
				var res=res.toJSON();
				
				//创建时间 格式化日期：yyyy-MM-dd hh:mm:ss
				function checkTime(i){
					if (i<10){
						i="0" + i
					}
					return i;
				}
				var date = new Date(res.createdAt);
				var date_value=date.getFullYear() + '-' + (date.getMonth() + 1) + '-' + date.getDate() + ' ' + checkTime(date.getHours()) + ':' + checkTime(date.getMinutes()) + ':' + checkTime(date.getSeconds());
				
				// 最后一次被修改的时间
				var editdate = new Date(res.updatedAt);
				var editdate_value=editdate.getFullYear() + '-' + (editdate.getMonth() + 1) + '-' + editdate.getDate() + ' ' + checkTime(editdate.getHours()) + ':' + checkTime(editdate.getMinutes()) + ':' + checkTime(editdate.getSeconds());
				
				var text={};
				text['createdTime']= date_value;
				text['editTime']= editdate_value;
				text['title']= res.title;
				text['body']= res.body;
				text['id']= res.objectId;
				//存入定义好的对象中
				this.blog=text
			});
		}
	}
</script>

<style scoped>
	.blog>h4,.blog>article,.blog>p{margin: 0;font-size: 35px;text-align: left}
	.blog>h4,.blog>p{padding:0 50px;}
	.blog>p{font-size:25px;text-indent:3em;color: #000000;}
	.blog>article{padding:50px;color: #969799;text-indent:2em;}
	.blog>h4{padding:50px 0;text-align: center;}
	.cz{padding:50px;display: flex;justify-content: center;}
	.but:first-child{margin-right: 20px;}
	.but:last-child{margin-left: 20px;}
	.wrapper {
	    display: flex;
	    align-items: center;
	    justify-content: center;
	    height: 100%;
	}
	.block{
		display: flex;
		align-items: center;
		justify-content: center;
		height:500px;
	}
	.block div{
		display: block;
		font-size:36px;
		color: #07c160;
	}
</style>
