<template>
	<div class="index">
		<van-nav-bar fixed placeholder title="博客" @click-right="onClickRight">
			<template #right>
				<van-icon name="search" size="18" />
			</template>
		</van-nav-bar>
		<!-- 遮罩层搜索框 -->
		<van-overlay :show="show" @click="show = false">
			<div @click.stop>
				<van-search
					v-model="value"
					show-action
					placeholder="请输入搜索博客标题"
					@search="onSearch"
					@cancel="onCancel"
				/>
			</div>
		</van-overlay>
		<!-- 轮播图 -->
		<van-swipe :autoplay="3000">
			<van-swipe-item v-for="(image, index) in images" :key="index">
				<img v-lazy="image" @click="Click(index)"/>
			</van-swipe-item>
		</van-swipe>
		<!-- 通知栏 -->
		<van-notice-bar background="#ffffff" @click="notice();" :text="title" left-icon="volume-o" />
		<!-- 博客列表 -->
		<van-divider :style="{ color: '#1989fa', borderColor: '#1989fa', padding: '0 16px' }">
			<h3>博客内容</h3>
		</van-divider>
		<div class="blog" v-for="(v,i) in searchs" :key="i">
			<router-link :to="'/ShowBlog/' + v.id">
				<h4 v-rainbow>{{v.title}}</h4>
				<p>发布日期：{{v.createdTime}}</p>
				<p>上次编辑：{{v.editTime}}</p>
				<article>{{v.body | text}}</article>
				<van-divider />
			</router-link>
		</div>
	</div>
</template>

<script>
	export default{
		name:"index",
		data(){
			return{
				title:'有些人浅薄，有些人沉默，有些人金玉其外，有些人内在光华。但每个人都会在某一天，遇到一个彩虹般绚丽的人。当你遇到了这个人，就会觉得其他一切都是浮云。',
				images: [
					require('../assets/banner1.jpg'),
					require('../assets/banner2.jpg'),
				],
				blogs:[],
				search:true,
				value:'',
				show:false,
			}
		},
		methods:{
			onClickRight() {
				this.show=true
			},
			notice(){
				this.$dialog.alert({
				  message:this.title,
				  width:'325px'
				}).then(() => {
					// on close
				});
			},
			Click(index){
				if(index==0){
					this.$toast('图片来源 ——《玉子市场》')
				}
				if(index==1){
					this.$toast('图片来源 ——《冰菓》')
				}
			},
			onSearch(val) {
				this.show=false
			},
			onCancel() {
				this.show=false
			}
		},
		created(){
			// var that=this;
			// this.$axios('/posts').then(function(res){
			// 	that.blogs=res.data.slice(0,10);//截取10个
			// }).catch(function(err){
			// 	console.log(err)
			// })
			
			//leancloud
			const AV=this.$AV;
			const query = new AV.Query('Blogs');
			
			// 按 createdAt 降序排列
			query.descending('createdAt');
			
			query.find().then((res)=>{
				// console.log(res)
				res.forEach((v,i)=>{
					// console.log(v)
					var text=v.attributes;
						//存入id
						text['id']=v.id;

						//创建时间 格式化日期：yyyy-MM-dd hh:mm:ss
						function checkTime(i){
							if (i<10){
								i="0" + i
							}
							return i;
						}
						var date = new Date(v.createdAt);
						var date_value=date.getFullYear() + '-' + (date.getMonth() + 1) + '-' + date.getDate() + ' ' + checkTime(date.getHours()) + ':' + checkTime(date.getMinutes()) + ':' + checkTime(date.getSeconds());
						// 存入创建时间(格式化后)
						text['createdTime']= date_value;
						
						// 最后一次被修改的时间
						date = new Date(v.updatedAt);
						date_value=date.getFullYear() + '-' + (date.getMonth() + 1) + '-' + date.getDate() + ' ' + checkTime(date.getHours()) + ':' + checkTime(date.getMinutes()) + ':' + checkTime(date.getSeconds());
						text['editTime']= date_value;

					//存入定义好的数组中
					this.blogs.push(text)
				})
				// console.log(this.blogs)
			}, function (error){
				// console.log("异常")
				this.$notify({ type: 'danger', message: '请求失败' });
			});
		},
		computed:{
			searchs(){
				return this.blogs.filter((v)=>{
					return v.title.match(this.value)
				})
			}
		}
	}
</script>

<style scoped>
	.index{text-align: center;}
	.swipe{width: 750px;}
	img{width:650px;float: left;}
	.van-swipe{padding:50px;}
	h3{font-size:40px;}
	.blog h4,.blog article,.blog p{margin: 0;font-size: 35px;text-align: left}
	.blog h4,.blog p{padding:0 50px;}
	.blog p{font-size:25px;text-indent:3em;color: #000000;}
	.blog article{padding:50px;color: #969799;text-indent:2em;}
	.blog h4{padding:50px 0;text-align: center;}
</style>
