<template>
	<div class="Addblog">
		<van-nav-bar fixed placeholder left-text="首页" @click-left="onClickLeft" left-arrow title="添加博客"></van-nav-bar>
		<!-- 发布前显示 -->
		<form v-if="fb">
			<label for="title">标题</label>
			<input type="text" name="title" id="title" v-model="blog.title"/>
			<label for="content">内容</label>
			<textarea type="text" name="content" id="content" v-model="blog.content"/>
			<van-button class='but' type="primary" @click.prevent="post">发布</van-button>
		</form>
		<!-- 发布成功显示 -->
		<div v-else-if="!fb" class="block">
			<div>博客发布成功</div>
		</div>
		<!--点击发布后显示加载中的遮罩层-->
		<van-overlay :show="show">
			<div class="wrapper" @click.stop>
				<van-loading type="spinner" color="#1989fa"/>
			</div>
		</van-overlay>
	</div>
</template>

<script>
	export default{
		name:"Addblog",
		data(){
			return{
				blog:{
					title:'',
					content:''
				},
				show: false,
				fb:true
			}
		},
		methods:{
			post(){
				if(this.blog.title==''){
					this.$toast('请输入标题')
				}else if(this.blog.content==''){
					this.$toast('请输入内容')
				}else{
					// 加载中的遮罩层
					this.show=true;
					
					// this.$axios({
					// 	method: 'post',
					// 	url: '/posts',
					// 	data: {
					// 		title: this.blog.title,
					// 		body: this.blog.content
					// 	}
					// }).then(function (response) {
					// 	console.log(response);
					// 	this.$toast('发布成功！')
					// }).catch(function (error) {
					// 	console.log(error);
					// });
					
					// 上传到leancloud
					// 为属性赋值
					this.$blogs.set('title', this.blog.title);
					this.$blogs.set('body', this.blog.content);
					// 将对象保存到云端
					this.$blogs.save().then((res) => {
						// console.log('保存成功。objectId:'+res.id)
						// console.log(`保存成功。objectId：${res.id}`);
						this.blog.title="";
						this.blog.content="";
						this.show=false;
						this.fb=false;
						this.$notify({ type: 'success', message: '发布成功' });
					}, (error) => {
						this.show=false;
						this.$notify({ type: 'danger', message: '发布失败' });
					});
				}
			},
			onClickLeft() {
				this.$router.push({name:'index'})
			},
		},
		//销毁前刷新页面
		beforeDestroy() {
			setTimeout(function(){
				window.location.reload()
			},1)
		}
	}
</script>

<style scoped>
	td{table-layout : fixed}
	form{padding: 50px;padding-top:0;text-align: center;}
	form textarea,form input{display: block;width:548px;}
	form label{display: block;font-size:50px;}
	form input,form textarea{font-size:30px;border: 1px solid gainsboro;padding:50px;}
	form textarea{resize:none;height:300px;}
	form>label,form>input,form>textarea,form>.but{
		margin-top: 50px;
	}
	.wrapper {
	    display: flex;
	    align-items: center;
	    justify-content: center;
	    height: 100%;
	}
	.block{
		display: flex;
		align-items: center;
		justify-content: center;
		height:500px;
	}
	.block div{
		display: block;
		font-size:36px;
		color: #07c160;
	}
</style>
