import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import axios from 'axios'
import 'amfe-flexible'
import { Button,Icon,Image as VanImage,Col, Row,NoticeBar,Dialog,Swipe, SwipeItem,Lazyload,NavBar,Toast,Tabbar, TabbarItem,Panel,Skeleton,Divider,Search,Overlay,Notify,Loading} from 'vant';

// 全局引入vant组件
Vue.use(Button);
Vue.use(Icon);
Vue.use(VanImage);
Vue.use(Col);
Vue.use(Row);
Vue.use(NoticeBar);
Vue.use(Dialog);
Vue.use(Swipe);
Vue.use(SwipeItem);
Vue.use(Lazyload);
Vue.use(NavBar);
Vue.use(Toast);
Vue.use(Tabbar);
Vue.use(TabbarItem);
Vue.use(Panel);
Vue.use(Skeleton);
Vue.use(Divider);
Vue.use(Search);
Vue.use(Overlay);
Vue.use(Notify);
Vue.use(Loading);

// 全局引入用axios
Vue.prototype.$axios=axios
// axios.defaults.baseURL='/api'//axios默认请求地址

// 阻止启动生产消息
Vue.config.productionTip = false

// 注册自定义指令
Vue.directive('rainbow',{//v-rainbow
	bind(el,binding,vnode){
		el.style.color= "#"+Math.random().toString(16).slice(2,8);//随机颜色
	}
})

//自定义过滤器
 Vue.filter('text',function(v){//博客内容截取26个字
	 return v.slice(0,26)+'...'
 })

//引入leancloud初始化配置
import blogs from './leancloud/config'
Vue.use(blogs)

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
